import { useTheme } from '@mui/material/styles';
import addIcon from 'assets/images/Sejong.png'; //      

export default function LogoIcon() {
  const theme = useTheme();

  return (
    <img
      src={addIcon}
      alt="ADD Icon"
      width="29"
      height="129"
      style={{ objectFit: 'contain' }}
    />
  );
}
